import React from 'react';
import { Button, StandardTextFieldProps, TextField } from '@mui/material';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import PropTypes from 'prop-types';
import Paper from '@mui/material/Paper';

import { formatEthFromWei } from '../Formatters';
import { TeamsResponse, _teamResponse } from '../assets/abi/farm-contract';
import { toWeiDecimals, IS_TOKEN_9_DECIMALS } from '../utils';

interface AirdropPanelProps {
  style?: React.CSSProperties;
  balance?: string;
}

const is9decimals = IS_TOKEN_9_DECIMALS;

function TabPanel(props: any) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

class AirdropPanel extends React.Component<AirdropPanelProps> {

  private airdropAmountInput: React.RefObject<StandardTextFieldProps>;
  private airdropRecipientInput: React.RefObject<StandardTextFieldProps>;
  private teamAirdropRecipientInput: React.RefObject<StandardTextFieldProps>;
  private teamAirdropAmountInput: React.RefObject<StandardTextFieldProps>;

  state = {
    tabValue: 1,
    balance: ""
  }
  baseState: any;
  _stateInterval: any;
  balance?: string;

  constructor(props: AirdropPanelProps) {
    super(props);

    this.baseState = this.state;
    this.airdropAmountInput = React.createRef();
    this.airdropRecipientInput = React.createRef();
    this.teamAirdropRecipientInput = React.createRef();
    this.teamAirdropAmountInput = React.createRef();

    // properties
    this.balance = props.balance;
  }

  componentDidMount = () => {

    this.checkState();
    if (this._stateInterval) {
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval + (window.dataRefreshInterval / 100 * 30 /*40% slower*/));
  }

  componentWillUnmount = () => {


  }

  componentWillReceiveProps(props: AirdropPanelProps) {
    this.balance = props.balance;
    this.setState({ balance: this.balance });
  }

  checkState = () => {
    if (window.connected !== true) {
      this.setState(this.baseState);
      return;
    }

  }

  tabHandleChange = (event: any, newValue: any) => {
    this.setState({ tabValue: newValue });
  }

  onClick_SendAirdrop = () => {
    if (!this.airdropAmountInput.current || !this.airdropRecipientInput.current) {
      return;
    }
    let recipient = this.airdropRecipientInput.current.value as string;

    // send to address
    if (window.web3.utils.isAddress(recipient)) {
      const inputValue = this.airdropAmountInput.current.value as string;
      const inputWeiValue = toWeiDecimals(inputValue, is9decimals);
      window.farmContract.methods.airdrop(recipient)
        .send({ value: inputWeiValue, from: window.walletAddress }).then((response: any) => {

        }).catch((err: any) => {
          console.log(err)
        });
      return;
    }

    // send to address from nickname
    if (!window.web3.utils.isAddress(recipient) && recipient.length > 0) {

      window.farmContract.methods.getAddressToNickname(recipient).call().then((result: string) => {
        console.debug("checked nickname ", recipient, " and got address ", result);
        if (result != "0x0000000000000000000000000000000000000000") {
          if (!this.airdropAmountInput.current) {
            return;
          }

          const inputValue = this.airdropAmountInput.current.value as string;
          const inputWeiValue = toWeiDecimals(inputValue, is9decimals);
          window.farmContract.methods.airdrop(result)
            .send({ value: inputWeiValue, from: window.walletAddress }).then((response: any) => {

            }).catch((err: any) => {
              console.log(err)
            });

        } else {
          alert("Invalid Sponsor Address");
        }
      });

      //alert("invalid recipient");
      return;
    }


  }

  onClick_SendTeamAirdrop = () => {
    if (!this.teamAirdropRecipientInput.current) {
      return;
    }
    let recipient = this.teamAirdropRecipientInput.current.value as string;

    // send to teamid
    if (!isNaN(parseInt(recipient))) {
      window.farmContract.methods.teams(recipient).call().then((result: TeamsResponse) => {
        if (result.created_at != "0") {
          if (!this.teamAirdropAmountInput.current) {
            return;
          }

          const inputValue = this.teamAirdropAmountInput.current.value as string;
          const inputWeiValue = toWeiDecimals(inputValue, is9decimals)
          window.farmContract.methods.teamAirdrop(recipient)
            .send({ value: inputWeiValue, from: window.walletAddress }).then((response: any) => {

            }).catch((err: any) => {
              console.log(err)
            });
        } else {
          alert("team not found");
        }
      });


      return;
    }
  }

  render = () => {
    return (
      <Paper className={"minHeight-small"} style={{ padding: "16px" }}>

        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={this.state.tabValue} onChange={this.tabHandleChange} aria-label="basic tabs example">
            <Tab label="Team Airdrop" />
            <Tab label="Direct Airdrop" />
          </Tabs>
        </Box>
        <TabPanel value={this.state.tabValue} index={0}>
          <TextField sx={{ my: 1 }} defaultValue={0} inputRef={this.teamAirdropRecipientInput} label={"Team Id"} type="number" fullWidth={true}></TextField>

          <TextField sx={{ my: 1 }} inputRef={this.teamAirdropAmountInput} defaultValue={0.1} label={"Amount in WALT"} type="number" fullWidth={true}></TextField>

          <Button sx={{ my: 1 }} disabled={window.connected != true} size="large" variant="contained" onClick={this.onClick_SendTeamAirdrop} fullWidth={true}>Send</Button>
          <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
            Balance:
          </Typography>
          <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
            {formatEthFromWei(this.state.balance, 6)} WALT
          </Typography>
        </TabPanel>


        <TabPanel value={this.state.tabValue} index={1}>

          <TextField sx={{ my: 1 }} inputRef={this.airdropRecipientInput} label={"Recipient"} type="text" fullWidth={true}></TextField>

          <TextField sx={{ my: 1 }} inputRef={this.airdropAmountInput} defaultValue={0.1} label={"Amount in WALT"} type="number" fullWidth={true}></TextField>

          <Button sx={{ my: 1 }} disabled={window.connected != true} size="large" variant="contained" onClick={this.onClick_SendAirdrop} fullWidth={true}>Send</Button>
          <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
            Balance:
          </Typography>
          <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
            {formatEthFromWei(this.state.balance, 6)} WALT
          </Typography>
        </TabPanel>


      </Paper>
    );
  }
}

export default AirdropPanel;