function isNumeric(value: string) {
    return /^-?\d+$/.test(value);
}

export const toWeiDecimals = (inputValue:string,is9decimals = false) => {

    if(!window.web3 || !window.web3.utils) {
        return 0
    }

    const isValidNumber = isNumeric(inputValue)

    if(!isValidNumber) {
        return 0;
    }

    if(!is9decimals) {
        return window.web3.utils.toWei(inputValue)
    } else {
        return window.web3.utils.toWei(inputValue, 'gwei');
    }
    
}


export const fromWeiDecimals = (inputValue:string,is9decimals = false) => {

    if(!window.web3 || !window.web3.utils) {
        return 0
    }

    const isValidNumber = isNumeric(inputValue)

    if(!isValidNumber) {
        return 0;
    }

    if(!is9decimals) {
        return window.web3.utils.fromWei(inputValue)
    } else {
        return window.web3.utils.fromWei(inputValue, 'gwei');
    }
    
}